body {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
}

.vkuiHeader {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;  
}

.div-center {
    display: table;
    margin: 0 auto;
}

.ProfileUser {
    display: flex;
    margin: -7px -7px 0 -7px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 32px;
}

.NameUser {
    margin-bottom: 8px; 
    margin-top: 20px
}

.SubheaderUser {
    margin-bottom: 24px;
    color: var(--text_secondary)
}

.TabbarItem--selected .vkuiIcon {
    -webkit-animation-name: boop;
            animation-name: boop;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
}

.Cell--disabled .vkuiIcon {
    -webkit-animation-name: boop;
            animation-name: boop;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
}

.vkuiPanelHeaderButton--vkcom {
    -webkit-animation-name: boop;
            animation-name: boop;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
}

@-webkit-keyframes boop {
    25% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }

    50% {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes boop {
    25% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
    }

    50% {
        -webkit-transform: scale(0.9);
        transform: scale(0.9);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}


/*renderjson*/
.renderjson a              { text-decoration: none; -webkit-user-select: text; user-select: text; }
.renderjson .disclosure    { color: crimson; font-size: 150%; -webkit-user-select: text; user-select: text; }
.renderjson .syntax        { color: grey; -webkit-user-select: text; user-select: text; }
.renderjson .string        { color: green; -webkit-user-select: text; user-select: text; }
.renderjson .number        { color: dodgerblue; -webkit-user-select: text; user-select: text; }
.renderjson .boolean       { color: plum; -webkit-user-select: text; user-select: text; }
.renderjson .key           { color: #318495; -webkit-user-select: text; user-select: text; }
.renderjson .keyword       { color: lightgoldenrodyellow; -webkit-user-select: text; user-select: text; }
.renderjson .object.syntax { color: grey; -webkit-user-select: text; user-select: text; }
.renderjson .array.syntax  { color: lightsalmon; -webkit-user-select: text; user-select: text; }

.vkuiTabbarItem--selected {
    color: #2B8FFE;
}

.vkuiPanelHeader__content-in {
    color: #2B8FFE;
}

.vkuiTabbarItem--android .vkuiTabbarItem__tappable {
    width: calc(100% + 16px);
    padding-bottom: 0px;
    border-radius: 50%;
}

.fixPaddingPlaceholder {
    margin-top: -25px;
    margin-bottom: -30px;
}

.podpis {
    display: flex;
    justify-content: center;
    white-space: pre-wrap;
}

.scroll {
    width: available !important;
    overflow-x: auto !important;
    word-wrap: normal;
}

.span.string {
    overflow-x: scroll !important;
    width: auto !important;
}

.div#responseTokenAccessToken.scroll {
    overflow-x: scroll !important;
    width: auto !important;
}

.start {
    justify-content: center;
    font-size: 50px;
}

.activeCellMenu {
    background-color: var(--button_secondary_background);
    border-radius: 8px
}

.btn_onboarding {
    margin-right: 5px;
    margin-left: 5px;
}

.btn_settings {
    margin-bottom: 5px;
}

.vkuiGallery__layer {
    cursor: default;
}

